//
// Theme Boxes
// --------------------------------------------------

.theme-box {
	.theme-box-title {
		padding: @grid-gutter-width/3;
		text-transform: uppercase;
		margin: 0;
		font-size: @font-size-base;
		font-weight: 400;
		> .view-more {
			float: right;
			font-size: 90%;
			text-transform: lowercase;
		}
	}
	.theme-box-content {			
		background-color: @gray-lighter;
		border: 1px solid @gray-light;
		border-top: none;
		&.expanded {
			padding: 0;
			.media-left {
				min-width:130px;
				padding: 0;
				background-size: cover;
				background-repeat: no-repeat;
				mark {
					display: block;
					white-space: nowrap;
					padding: 20px 10px;
					margin: 20% auto;
					background-color: rgba(0,0,0,0.5);
					color: white;
					text-transform: uppercase;
					font-size: 18px;
					font-weight: 300;
					max-width: 320px;
					text-overflow:ellipsis;
					overflow-x: hidden;
					a {
						&, &:hover, &:focus, &:visited, {
							color: white;
							text-decoration: none;
						}
					}
				}
				@media (max-width: @screen-sm) {
				    width: 100%;
				    float: left;
				    clear: both;
				}
			}
			.media-body {
				padding: @grid-gutter-width/2;
			}
		}
		&.lighter {
			background-color: white;
			+ .lighter {
				margin-top: @grid-gutter-width;
				border-top: 1px solid @gray-light;
			}
		}
		& {
			padding: @grid-gutter-width/2;
		}
		ul.list-unstyled {
			margin-bottom: 0;
		}
		h4 {
			line-height: 1;
			font-size: @font-size-base*1.1;
			font-weight: 600;
			margin: 0 0 @grid-gutter-width/3;
			&, a {
				&, &:hover, &:focus, &:visited {
					color: @gray-darker;
				}
			}
		}
		hr {
			border-top: 1px solid @gray-light;
			border-bottom: 1px solid white;
		}
		p {
			color: @gray-dark;
			margin: @grid-gutter-width/4 0 0;
		}
		@media (max-width:@screen-md) {				
			.featured-item, img.center-block {
				margin-bottom: 1em;
			}
		}
	}
	@media (max-width:@screen-md) {	
		img {
			width: 100%;
		}
	}
}


// Theme Box Colors
// -------------------------
.theme-box-primary {
	.theme-box-title { 
		background-color: @brand-primary; 
		&, > .view-more {
			color: contrast(@brand-primary, black, white, 60%);
		}
	}
	a {
		&, &:hover, &:focus, &:visited {
