//
// Newsletter
// --------------------------------------------------

.newsletter {
	.newsletter-box {
		background-color: @gray-lighter;
		padding: @grid-gutter-width/3 @grid-gutter-width/2 @grid-gutter-width/2;
		legend {
			font-size: @font-size-base*1.2;
			color: lighten(@brand-info, 20%);
			line-height: 1.4;
			margin: 0 0  @grid-gutter-width/3 0;
			font-weight: 600;
			border-bottom: 0;
			text-align: center;
		}
		p {
			color: @gray;
			text-align: center;
			margin:  0 0 @grid-gutter-width/3 0;
		}
		.form-control {
			margin-bottom: @grid-gutter-width/2;
		}
		hr {
			border-bottom: 1px solid white;
			border-top: 1px solid @gray-light;
		}
	}
	@media (min-width:@screen-sm-min) and (max-width:@screen-md) {
		p, hr, .btn-facebook {
			display:none;
		}
	}
	@media (max-width:@screen-sm) {
		margin-top: @grid-gutter-width;
	}
}