/**
* eDirectory - Members Section
*/
.sponsor-area {

	/*Alert popup*/
	#fancybox-outer {
		border-radius: 0!important;
	}
	a:focus, a:active {
		border: none;
		outline: none;
	}
	.review-name a {
		font-size: 14px;
		font-weight: normal;
		color: #333;
	}
	.review-name a:hover {
		color: #1abc9c;
	}
	strong {
		font-weight: 600;
	}
	.member-form label > em {
		color: #999999;
		font-size: 10px;
		font-style: normal;
		font-weight: 400;
	}
	.wrapcaption {
		font-size: 12px;
	}
	/*Alerts */
	.alert {
		padding: 8px 35px 8px 14px;
		background-color: mix(@brand-warning, white, 15%);
		border: 1px solid mix(@brand-warning, white, 90%);
		text-shadow: none;
		color: @brand-warning;
	}
	.alert-success {
		background-color: mix(@brand-success, white, 15%);
		border: 1px solid  mix(@brand-success, white, 90%);
		;
	}
	.alert-success, .alert-success .alert-heading {
		color: @brand-success;
	}
	.alert-danger, .alert-error {
		background-color: mix(@brand-danger, white, 15%);
		border: 1px solid mix(@brand-danger, white, 90%);
	}
	.alert-danger, .alert-error, .alert-danger .alert-heading, .alert-error .alert-heading {
		color: @brand-danger;
	}
	.alert-info {
		background-color: mix(@brand-info, white, 15%);
		border: 1px solid mix(@brand-info, white, 90%);
	}
	.alert-info, .alert-info .alert-heading {
		color: @brand-info;
	}

	/*Menu Notification*/
	.notify {
		position: absolute;
		width: 20px;
		height: 20px;
		line-height: 20px;
		background-color: @brand-primary;
		color: white;
		text-align: center;
		font-size: 10px;
		font-style: normal;
		font-weight: 600;
		right: 0;
		top: 0;
		border-radius: 20px;
		pointer-events: none;
	}

	/*Login Options*/
	.complementaryInfo {
		font-size: 9px;
	}
	.loginComplementaryInfo {
		text-align: center;
	}
	.list-back {
		margin: 20px 0;
		list-style: none;
		padding: 0;
	}

	/* #Buttons */

	p.standardButton {
		text-align: center;
		display: inline;
	}
	.baseButtons p.standardButton {
		margin: 15px 0;
	}
	#img_customtime {
		padding: 10px;
		display: inline;
	}
	/* Admin Actions*/
	.admin-actions {
		display: block;
		width: 99.6%;
		padding: 10px 0 0;
		text-align: right;
	}
	.admin-actions .admin-btn {
		background-color: @brand-primary;
		color: #FFFFFF;
		font-size: 11px;
		line-height: 16px;
		padding: 10px 30px;
		text-transform: uppercase;
		border: none;
	}
	.admin-actions .admin-btn:hover, .admin-actions .admin-btn:focus {
		background-color: @brand-primary;
	}
	/*Warning Box*/
	table.warningBOXtext {
		background-color: #fcf8e3;
		border: 1px solid #fbeed5;
		color: #c09853;
	}
	table.warningBOXtext tr {
		display: block;
		padding: 15px 20px;
	}
	.warningBOXtext th {
		vertical-align: middle;
	}
	/* #Gallery*/

	a.ImageEditCaptions , a.ImageDelete, a.mainImageLinkBK {
		border-radius: 10px;
		padding:0.5em;
		color: @gray;
		background-color:lighten(@gray-light, 46%); 
		&:hover, &:focus {
			background-color:lighten(@gray-light, 35%);
			text-decoration:none;
		}
	}

	a.mainImageLink.mainImageLinkBK {
		background-color: @brand-primary;
		color: white; 
		&:hover, &:focus {
			background-color:lighten(@brand-primary, 10%);
		}
	}

	.Full ul li {
		background-color: #DEDEDE;
		border: 1px solid #C9C9C9;
	}
	.Full ul li h5 {
		font-size: 12px;
		font-weight: normal;
		color: #333;
		font-style: italic;
	}
	.Full ul li.mainImageGallery {
	}
	/*Warning Box to Pay*/
	a.warningBOXcontent {
		color: #fff;
		font-size: 13px;
		line-height: 16px;
		font-weight: normal;
		padding: 10px 20px;
		margin-left: 50px;
		background-color: #2ECC71;
		border: none;
		white-space: nowrap;
	}
	a.warningBOXcontent {
		font-size: 11px;
		font-weight: normal;
	}
	a.warningBOXcontent:hover {
		background-color: #58D68D;
	}
	.warningBOX td a {
		display: none;
	}
	.warningBOX td {
		margin-left: -10px;
	}
	.warningBOXtext {
		font-size: 12px;
		padding: 10px;
	}
	/* # ALL Standard Table
	*/


	[class*="standard-table"] {
		color: @gray-darker;
		background-color: lighten(@gray-light, 47%);
		border: 1px solid lighten(@gray-light, 40%);
		p {
			font-size: 12px;
		}
		.highlight, input[type="file"].highlight {
			border-color: @brand-success;
			background-color: mix(@brand-success, white, 5%);
			&.successMessage {
				color: @brand-success;
			}
		}
	}
	.standard-table-putradio {
		background-color: #000;
	}
	.ie .standard-table-putradio {
		background-color: transparent;
	}
	[class*="standard-table"] tr {
		border: 1px solid #efefef;
	}
	.ie7 [class*="standard-table"] td, .ie7 [class*="standard-table"] th {
		border: 1px solid #ddd;
	}
	[class*="standard-table"] table tr {
		border: none;
	}
	[class*="standard-table"] i span {
		font-size: 9px;
		font-style: normal;
		color: #999;
	}
	[class*="standard-table"] i {
		font-style: normal;
		font-size: 16px;
		color: #666;
	}
	[class*="standard-table"] td strong {
		font-size: 14px;
		margin: 0 2px 0 -8px;
	}
	.right [class*="standard-table"] td strong, [class*="standard-table"] td select + strong {
		margin-left: 0;
	}
	[class*="standard-table"] td p.warningBOXtext strong, [class*="standard-table"] td span > strong, [class*="standard-table"] .tableCategoriesTITLE strong, [class*="standard-table"] .standard-tableContent strong {
		margin: 0;
		padding: 5px;
	}
	/* #Only STandard-table*/
	.standard-table {
		th {
			font-weight: 400;
			font-size: 13px;
		}
		.standard-tabletitle {
			background-color: lighten(@gray-light, 40%);
			font-size: 13px;
			font-weight: 600;
			padding: 6px 0 7px 15px;
			text-align: left;
			span {
				font-size: 11px;
				font-weight: 400;
				display: inline!important;
			}
		}
		&.tabsTable {
			background-color: transparent;
			border: none;
			tr {
				background-color: transparent;
				border: none;
			}
		}
	}
	.well-steps {
		background-color: lighten(@gray-light, 40%);
		border: 1px solid @gray;
		padding: 10px;
		text-align: center;
		p {
			padding: 10px 0;
			font-size: 13px;
		}
		textarea {
			height: 100px;
		}
	}
	.well-text {
		p {
			line-height: 2em;
			font-size: 1em;
			padding-bottom: 10px;
		}
	}
	/*FAQ */

	.faq-search {
		h4 {
			font-size: 1.3em;
		}
	}
	/* #Standard Table Top Blue*/
	.tableTOPBLUECheck {
		background-color: lighten(@gray-light, 40%);
		border: 1px solid lighten(@gray-light, 28%);
	}
	.tableTOPBLUECheck td {
		text-align: left;
	}
	.standard-tableTOPBLUE {
		border: 1px solid lighten(@gray-light, 28%);
		background-color: white;
		text-align: center;
	}
	.standard-tableTOPBLUE th {
		background-color: lighten(@gray-light, 40%);
		font-weight: normal;
	}
	.standard-tableTOPBLUE td, .standard-tableTOPBLUE th {
		border: 1px solid lighten(@gray-light, 28%);
	}
	.standard-tableTOPBLUE th {
		color: #000;
	}
	.standard-tableTOPBLUE .innerTable td {
		border-left: none;
	}
	.standard-tableTOPBLUE .hideForm td {
		border: none;
	}
	.standard-tableTOPBLUE.arrow-manage {
		font-size: 0.9em;
	}
	/* Table Level */
	.levelTable th, .levelTable td {
		font-weight: normal;
		font-size: 1em;
		text-align: center;
	}
	.levelTitle {
		background-color: @brand-primary;
		color: white;
	}
	.levelTopdetail {
		background-color: mix(@brand-primary, white, 70%);
		color: #fff;
	}
	.levelTable .tableOption {
		text-align: left;
		background-color: lighten(@gray-light, 40%);
	}
	.levelTable .tableOption a {
		color: #666;
	}
	.levelTable .tableOption a:hover {
		text-decoration: underline;
	}
	.levelTable .tableOption a:before {
		background: url("images/iconography/glyphicons-halflings.png") no-repeat scroll -25px -45px transparent;
		content: "";
		display: block;
		height: 18px;
		width: 16px;
		float: left;
		opacity: 0.7;
	}
	.levelTabe table {
		color: #888;
	}
	/* #Standard Inner Table*/
	.standard-innerTable {
		width: 98%;
		border: 1px solid #ccc;
		margin: 1%;
	}
	.standard-innerTable td, .standard-innerTable th {
		border: 1px solid #ccc;
	}
	/*Payment Method*/

	.standardChooseLevel td {
		border-bottom: none;
	}
	/* #Table Payment Method*/
	[class*="standard-table"] .paymentMethods td {
		border: none;
		font-size: 10px;
	}
	/* #Table Categories */
	.tableCategoriesTITLE {
		background-color: #E9E9E8;
	}
	.tableCategoriesTITLE strong {
		font-weight: 600;
	}
	/* #Tree-view Special */
	.categoryTreeview {
		background-color: #fff;
		border-radius: 4px;
		border: 1px solid #dedede;
	}
	/*#Sidebar*/
	.previewmember .advertisement > h2 {
		margin-bottom: 20px;
		padding-bottom: 10px;
		border-bottom: 1px solid @gray-light;
	}
	.memberMenu {
		margin-bottom: 20px;
		clear: both;
	}
	.user-info {
		p {
			font-size: 0.9em;
			display: block;
			span {
				display: block;
				margin-bottom: 10px;
			}
			a {
				color: @link-color;
				&:hover {
					color: @link-color;
				}
			}
		}
	}
	/* MEMBERS Manage Account Form */
	.member-form label {
		cursor: default;
	}
	.member-form .right {
		background-color: @body-bg;
		border-left: 1px solid @gray-lighter;
	}
	.member-form .left span {
		font-weight: normal;
		color: #696969;
	}
	blockquote.high {
		background-color: #f9f9f9;
	}
	blockquote p {
		font-size: 11px;
	}
	blockquote.low {
		background-color: #fafafa;
	}
	a.remove {
		background: url(images/iconography/bg_switch_close.gif) no-repeat center;
	}
	.member-activity {
		.activity {
			border: 1px solid @gray;
			border-bottom: none;
			p {
				font-size: 0.9em;
				line-height: 1.6em;
			}
		}
		.option {
			background-color: lighten(@gray-light, 40%);
			border: 1px solid @gray;
			a {
				font-size: .8em;
				line-height: .5em;
			}
		}
	}
	/*Icons */
	.sitemgr-icon-gear {
		background-position: 0 0;
		margin: 0 5px -3px;
	}
	[class*="sitemgr-icon"] {
		background-image: url(images/iconography/sitemgr-icons.png);
		background-repeat: no-repeat;
		height: 16px;
		width: 16px;
		display: inline-block;
	}
	/* DASHBOARD MEMBERS 2014*/


	.webitem {
		border: 1px solid lighten(@gray-light, 40%);
		font-size: 1.4rem;
		.opacity(0.8);
		.transition(0.4s);
		.title {
			font-size: .9em;
		}
		p {
			font-size: 0.9em;
			font-weight: 300;
		}
		b {
			font-weight: 600;
			color: @gray-darker;
		}
		.desc {
			background-color: lighten(@gray-light, 48%);
			p span {
				color: @brand-warning;
				font-size: 0.9em;
				font-style: italic;
				font-weight: 600;
			}
			span.status {
				position: relative;
				text-align: center;
				> span {
					font-style: normal;
					font-weight: 300;
					font-size: .9em;
					cursor: default;
					&:before {
						content: "";
						display: block;
						margin: 0 auto;
						height: 20px;
						width: 20px;
						border-radius: 20px;
						overflow: hidden;
					}
					&.status-active {
						color: @brand-success;
						&:before {
							background-color: @brand-success;
						}
					}
					&.status-suspended {
						color: @brand-warning;
						&:before {
							background-color: @brand-warning;
						}
					}
					&.status-expired {
						color: @brand-danger;
						&:before {
							background-color: @brand-danger;
						}
					}
					&.status-pending {
						color: @brand-warning;
						&:before {
							background-color: @brand-warning;
						}
					}
				}
			}
		}
		.action {
			background-color: lighten(@gray-light, 40%);
			font-size: 0.9em;
			font-weight: 300;
			color: @link-color;
		}
		&.active, &.active:hover {
			border-color: mix(@brand-success, white, 70%);
			box-shadow: 1px 2px 2px @gray-lighter;
			.opacity(1);
			.desc {
				background: white;
			}
		}
		&:hover {
			border-color: mix(black, white, 15%);
			box-shadow: 0 0 2px @gray;
			.desc {
				cursor: pointer;
			}
		}
	}
	.addcontent {
		border: 1px solid lighten(@gray-light, 40%);
		font-size: 1.2rem;
		ul {
			list-style: none;
		}
		> p {
			font-size: 1.18em;
			line-height: 1em;
			color: @gray-darker;
			border-bottom: 1px solid lighten(@gray-light, 40%);
		}
		a {
			color: @brand-success;
			&:hover {
				color: mix(@brand-success, black, 50%)
			}
		}
	}
	.alert-new {
		background-color: @brand-warning;
		font-style: normal;
		font-size: 11px;
		color: white;
		border-radius: 10px;
		line-height: 10px;
	}
	.dashboard {
		border: 1px solid lighten(@gray-light, 40%);
		color: @gray-darker;
		header {
			border-bottom: 1px solid lighten(@gray-light, 40%);;
			h1 {
				font-weight: 400;
				font-size: 1.6em;
				line-height: 1;
			}
			.btn {
				font-weight: 600;
				font-size: 1em;
			}
			p {
				font-weight: 300;
				line-height: 0.9em;
				font-size: 0.9em;
				margin: 9px 0 5px;
				b {
					font-weight: 600;
				}
			}
		}
		section {
			h2 {
				font-weight: 400;
				font-size: 1.2em;
				text-transform: uppercase;
				line-height: 1.2em;
			}
			&.stats-summary {
				.row-fluid > div {
					border-left: 1px solid @gray-lighter;
					&:first-child {
						border: none;
					}
				}
				h1 {
					color: @brand-primary;
					font-weight: 400;
					font-size: 2.6em;
					line-height: 0.9em;
				}
				h5 {
					color: @brand-primary;
					font-weight: 600;
					font-size: 1.2em;
					line-height: 1em;
				}
				p {
					color: @gray;
					font-size: 0.8em;
					line-height: 1.4em;
				}
			}
			&.reviews-list {
				.head p {
					color: @gray;
					font-size: 0.9em;
				}
				.paging {
					text-align: right;
				}
				.viewmore {
					text-align: center;
					font-size: 0.9em;
					line-height: 3em;
				}
			}
		}
		.game-completion {
			background-color: lighten(@gray-light, 48%);
			border-bottom: 1px solid lighten(@gray-light, 40%);
			h5 {
				font-weight: 600;
				font-size: 1em;
				line-height: 1.8em;
			}
			p {
				font-size: 0.9em;
				font-weight: 400;
				color: @gray-dark;
			}
			.step {
				color: @gray;
				font-size: 13px;
				&.large-step {
					text-align: right;
				}
				a, a:visited {
					color: @brand-success;
					line-height: 1.7em;
				}
				a:hover {
					color: @link-color;
				}
			}
		}
		.completion-chart {
			position: relative;
			span {
				font-weight: 600;
			}
			input {
				color: #333 !important;
				font-family: 'Open Sans', sans-serif !important;
				font-weight: 600 !important;
			}
		}
		.item-review {
			border: 1px solid @gray-light;
			background-color: lighten(@gray-light, 40%)er;
			.transition(0.3s);
			.stars-rating [class*="rate-"] {
				background-color: lighten(@gray-light, 40%)er;
			}
			.review-text, .review-reply {
				font-size: 0.9em;
				color: @gray;
			}
			time {
				line-height: 2.2em;
				color: @gray;
				font-size: 0.9em;
				vertical-align: middle;
			}
			.review-summary {
				span, a {
					font-size: 0.9em;
					line-height: 2.2em;
				}
				b {
					font-weight: 600;
				}
				em {
					font-weight: 400;
					color: @gray-darker;
				}
				span {
					color: @brand-success;
					&.text-muted {
						color: @gray;
					}
					&.text-highlight {
						color: @brand-success;
					}
				}
				&:hover {
					cursor: pointer;
				}
				&.new {
					border: 1px solid @brand-success;
				}
			}
			.review-detail {
				a {
					font-size: 0.9em;
					text-transform: capitalize;
				}
				img {
					border: 1px solid @gray-light;
				}
				em {
					font-size: 0.9em;
					font-style: normal;
					color: @gray;
				}
				input[type="radio"], label {
					margin: 5px 10px 5px 0;
				}
			}
			&:hover {
				border-color: @gray;
				box-shadow: 0 0 3px @gray-light;
			}
		}
	}
	.chart-legends {
		ul {
			li {
				color: @gray;
				font-size: .85em;
				line-height: 25px;
				cursor: pointer;
			}
		}
		span {
			color: @gray-darker;
			font-size: .85em;
		}
		.hidden-legends {
			ul {
				list-style:none;
				margin: 0;
				padding: 0;
				background-color: white;
				box-shadow: 0 0 1px #ddd;
				.opacity(0);
				.transition(0.4s);
				li {
					border-top: 1px solid lighten(@gray-light, 40%);
					&:first-child {
						border-top: none;
					}
					&:hover {
						background-color: lighten(@gray-light, 40%)er;
					}
				}
			}
			&:hover {
				ul {				
					.opacity(1);
				}
			}
		}
	}
	.ie .chart-legends {
		.hidden-legends {
			ul {
				border: 1px solid @gray-lighter;
			}
		}
	}
	.minor-nav {
		ul {
			text-align: center;
			border-bottom: 1px solid lighten(@gray-light, 45%);
			li {
				a {
					background-color: lighten(@gray-light, 48%);
					line-height: 39px;
					padding: 10px 30px;
					&:hover, &.active {
						background-color: @brand-primary;
						color: white;
					}
				}
			}
		}
	}
	.sitemgr-pagination.top-pagination {
		border-bottom: none;
	}
	/* TABLE ITEM LIST*/
	.table-itemlist {
		.main-options {
			b {
				color: @link-color;
			}
		}
		[class*="status-"] {
			background-color: transparent;
		}
		.status-pending {
			color: @brand-warning;
		}
		.status-suspended {
			color: @brand-success;
		}
		.status-expired {
			color: @brand-danger;
		}
		.status-active {
			color: @brand-success;
		}
		th {
			background-color: lighten(@gray-light, 20%);
			border-bottom: none;
			color: white;
			font-size: 12px;
			font-weight: 600;
		}
		tr {
			border: 1px solid lighten(@gray-light, 40%);
			&:nth-child(2n) {
				background-color: @gray-lighter;
			}
		}
		.standard-innerTable {
			.tableTitle {
				background-color: lighten(@gray-light, 40%);
				border: 1px solid @gray-light;
			}
			th {
				font-weight: 600;
				background-color: transparent;
				border: 1px solid @gray-light;
				color: @gray-dark;
			}
			tr {
				background-color: white;
			}
			td {
				border: 1px solid @gray-light;
			}
		}
	}
	/* Image Gallery*/
	.icon-ion-ios7-trash-outline, .icon-edit38, .icon-ion-ios7-close-empty {
		background: url(images/iconography/icons-image-gallery.png) no-repeat  0 0 transparent;
		height: 27px;
		width: 27px;
		float: left;
	}
	.icon-edit38 {
		background-position: 0 -29px ;
	}
	.icon-ion-ios7-close-empty {
		background-position: 0 -56px;
	}
	.content-pills {
		.form-group {
			label {
				font-size: 14px;
			}
		}
	}
	.panel {
		.panel-heading {
			input[type="file"] {
				background-color: #fcfcfc;
				border: 1px solid #ddd;
				height: 18px;
				margin-right: 10px;
				padding: 5px;
				line-height: 1;
			}
		}
	}
	.template-download {
		background-color: @gray-lighter;
		.col-sm-3.pull-right {
			text-align: right;
			padding-top: 20px;
		}
		.btn-primary {
			background-color: @brand-info;
		}
	}
	#cropSection {
		font-size: 14px;
		color: @gray-darker;
		background-color: lighten(@gray-light, 40%);
		border: 1px solid @gray-light;
		.nav-pills {
			border-bottom: 1px solid @gray-light;
			> li {
				> a {
					line-height: 20px;
					margin-bottom: -1px;
				}
				&.active  > a {
					background-color: @brand-primary;
					color: white;
				}
			}
		}
		.modal-footer {
			text-align: center;
			background-color: rgba(255, 255, 255, 0);
		}
	}
	
}