
.pricing-table {
  float: left;
  margin: 28px 0 40px 1.5%;
  width: 97%;
  text-align: center;
  blockquote {
    text-align: left;
    max-width: 98%;
    padding: 0;
    border: none; 
    font-size: 1em;
  }
  .pricinglevel {
    width: 215px;
    padding: 15px;
    display: inline-table;
    text-align: center;
    background-color: @gray-lighter;
    border: 1px solid @gray-light;
    margin: 0 5px 20px;
    font-size: 1em;
    word-wrap: break-word;  
    .btn {
      display: block;
      width: auto;
      margin: 10px auto;
    }
    .hgroup {
      display: inline-block;
    }
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
    }
     h1,
     h2 {
      font-size: 2.5em;
      border-bottom: 1px solid lighten(@gray-light, 40%);
      margin-bottom: 1em;
    }
     h1 small,
     h2 small {
      font-size: 80%;
    }
     h1 span,
     h2 span {
      font-size: .6em;
      white-space: nowrap;
    }
     h2 {
      margin-bottom: -5px;
      border: none;
    }
     h3 {
      font-size: 1.8em;
      font-weight: normal;
      color: @gray-dark;
    }
     h3 small {
      font-size: .9em;
    }
     h3 span {
      font-size: 0.4em;
      text-transform: none;
      font-family: @font-family-base;
    }
     h4 {
      font-size: 14px;
      font-weight: 400;
      color: #333;
      line-height: 30px;
      text-transform: uppercase;
      margin: 10px 0;
    }
     h6 {
      font-size: 1em;
      font-weight: 400;
      color: #a3abb0;
    }
     em {
      background-color: #d9e3e9;
      display: block;
      font-size:  1.2em;
      font-style: normal;
      font-weight: normal;
      height: 1px;
      line-height: 0;
      margin: 25px 25px 15px;
      text-transform: uppercase;
    }
     em span {
      background-color: #f3f7f8;
      padding: 0 0 0 7px;
    }
     ul li {
      font-size: 13px;
      line-height: 22px;
    }
     ul li.disabled {
      color: #9cb4bf;
      text-decoration: line-through;
    }
    &.most-popular {
      box-shadow: 0 1px 8px #CDCDCD;
      padding: 10px 30px 20px;
      margin: 0 -10px;
      background-color: #fff;
      position: relative;
      z-index: 99;
      h5 {
        font-size: 1.3em;
        line-height: 1.2;
        text-transform: uppercase;
        font-weight: 400;
        color: #2980b9;
      }
      em span {
        background-color: #fff;
        padding: 0 0 0 7px;
      }
    }
  }

  .lower-level {
    float: right;
    margin: 25px 0;
    width: 217px;
    .pricinglevel {
      margin-top: 0;
    }
    &.up {
      margin-top: 0;
    }
  }

  .contact-info {
    padding: 15px;
    margin: 5px 0 20px 5px;
    border: 1px solid #d9e3e8;
    h2,
    p {
      font-size: 14px;
      font-weight: 400;
      text-align: center;
      word-wrap: break-word;
    }
    h2 {
      line-height: 26px;
      margin: 4px 0 15px;
      text-transform: uppercase;
    }
    p {
      color: #2980b9;
    }
    hr {
      margin: 10px 0;
    }
  }
}


@media (max-width: @screen-md) {
  .tabs-advertise > li {
    margin: 5px 0;
    width: 100%;
    max-width: 100%;
  }
  .tabs-advertise > li a {
    font-size: 1em;
    overflow: hidden;
    padding-left: 2%;
    padding-right: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
  }
  .pricing-table {
    margin: 28px 0 40px;
    width: 100%;
  }
  .pricing-table .pricinglevel {
    width: 100%;
    text-align: center;
    padding: 10px 1%;
    margin-bottom: 20px;
  }
  .pricing-table .pricinglevel hgroup,
  .pricing-table .pricinglevel.lower-level hgroup {
    float: none;
    display: block;
    width: auto;
    padding: 0 10px;
    margin-right: 0;
    text-align: center;
    border-right: none;
  }
  .pricing-table .pricinglevel hgroup h1,
  .pricing-table .pricinglevel.lower-level hgroup h1 {
    border-bottom: 1px solid #ccc;
    line-height: 1;
  }
  .pricing-table .pricinglevel hgroup h1 span,
  .pricing-table .pricinglevel.lower-level hgroup h1 span {
    white-space: nowrap;
  }
  .pricing-table .pricinglevel .text-underline {
    display: none;
  }
  .pricing-table .pricinglevel .btn,
  .pricing-table .pricinglevel.lower-level .btn {
    float: none;
    dsplay: block;
    width: auto;
  }
  .pricing-table .pricinglevel ul li {
    float: left;
    font-size: 1em;
    padding-right: 7px;
    white-space: nowrap;
    width: auto;
    word-wrap: break-word;
  }
  .pricing-table .pricinglevel.most-popular {
    padding: 10px 1.5%;
    margin: 0 0 20px 0;
  }
  .pricing-table .lower-level {
    width: 100%;
  }
  .pricing-table .lower-level .contact-info {
    float: left;
    padding: 10px 1%;
    width: 100%;
    margin: 0;
  }
  .pricing-table .lower-level .contact-info h2 {
    line-height: 1.7;
  }
  .pricing-table .pricinglevel h1, .pricing-table .pricinglevel h2 {
    margin-bottom: 0;
  }
}
