//
// Reviews
// --------------------------------------------------

// Reviews Stars
// -------------------------
.stars-rating {
	display: inline-block;
	height: 13px;
	[class*="rate-"]:after {
		.fa-icon();
		color: @brand-success;
		font-size: 12px;
		vertical-align: middle;
	}
	.rate-0:after {
		content: @fa-var-star-o @fa-var-star-o @fa-var-star-o @fa-var-star-o @fa-var-star-o;
	}
	.rate-1:after {
		content: @fa-var-star @fa-var-star-o @fa-var-star-o @fa-var-star-o @fa-var-star-o;
	}
	.rate-2:after {
		content: @fa-var-star @fa-var-star @fa-var-star-o @fa-var-star-o @fa-var-star-o;
	}
	.rate-3:after {
		content: @fa-var-star @fa-var-star @fa-var-star @fa-var-star-o @fa-var-star-o;
	}
	.rate-4:after {
		content: @fa-var-star @fa-var-star @fa-var-star @fa-var-star @fa-var-star-o;
	}
	.rate-5:after {
		content: @fa-var-star @fa-var-star @fa-var-star @fa-var-star @fa-var-star;
	}
	&.large {
		height: 25px;
		[class*="rate-"]:after {
				font-size: 30px;
			}
	}

}

// Button review (Open Modal)
// -------------------------
button.star-rating {
	display: inline-block;
	touch-action: manipulation;
	padding: 0;
	cursor: pointer;	
	background-image: none;
	background-color: transparent;
	border: none;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
	border-radius: 0;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	.stars-rating {
		[class*="rate-"]:after {
			font-size: 16px;
		}
		&.large {
			height: 50px;
			[class*="rate-"]:after {
					font-size: 30px;
				}
		}

	}

}

// View all reviews buttons
// ---------------------------
.rate {
	> a.rating-count {
		 vertical-align: sub;
	}
}


// Review Box
// -----------------------
.review-box {
	background-color: white;
	border: 1px solid @gray-light;
	margin-bottom: @grid-gutter-width;
	.review-top {
		padding: @grid-gutter-width/2;
		background-color: @gray-lighter;
		border-bottom: 1px solid @gray-light;
		h5 {
			margin-top: 0;
			font-weight: 400;
		}
		q {
			color: @gray;
		}
	}
	.review-bottom {
		vertical-align: middle;
		padding: @grid-gutter-width/3;
		p {
			margin-bottom: 0;
			margin-top: 6px;
			color: @gray;
		}
		button + span {
			padding: 5px 10px 4px;
			border: 1px solid @gray-light;
			border-radius: 3px;
			float: right;
		}
	}
}








