//
// Detail 
// --------------------------------------------------

// Detail Items
// -------------------------


.detail {
	position: relative;
	.media > .media-left {
		padding-right: @grid-gutter-width;
	}
	h1, h2 {
		margin: 0 0 0.2em;
		line-height: 1;
		font-size: 2em;
		font-weight: 400;
		+ img {
			margin: @grid-gutter-width/2 auto;
		}
	}
	h3 {
		font-weight: 400;
		font-size: 1.4em;
	}
	hr {
		border-top-color: @gray-light;
	}
	section > p {
		line-height: 1.8;
		font-size: 1.1em;
		margin: 0.9em 0 2em;
		color: @gray-dark;
	}

	@media (max-width:@screen-md) and (min-width: @screen-sm), (max-width:@screen-smxs) {		
		> .media > .media-left {
			display: block;
			float: none;
			text-align: center;
			padding-right: 0;
		}
	}
	@media (max-width:@screen-md) and (min-width: @screen-sm), (max-width:@screen-smxs) and (min-width:@screen-xs) {
		> .media > .media-left {
			#galleria {
			  width: 100%;
			  height: 340px;
			}
		}
	}
	@media (max-width: @screen-xs) {		
		> .media > .media-left {
			#galleria {
			  width: 100%;
			  height: 200px;
			}
		}
	}
}

.details-sidebar {
	@media (max-width: @screen-sm) {
		.map {
			width: 100%;
			img {
				width:100%;
			}
		}
	}
}

// Deal Detail
// -------------------------
.deal-details {
	text-align: center;
	.description-details {
		display: -webkit-flex; /* Safari */
		display: flex; 
		-webkit-flex-direction: row;
		flex-direction: row;
		-webkit-align-items: center;
		align-items: center;
		-webkit-justify-content: center;
		justify-content: center;
		.info {			
		-webkit-flex: 1; /* Safari 6.1+ */
		    -ms-flex: 1; /* IE 10 */ 
		    	flex: 1;
		    	span, em {
		    		display: block;
		    		clear: both;
		    	}
		    	em {
		    		font-size: 1.7em;
		    		font-weight: 400;
		    		font-style: normal;
		    	}
		}
	}
	p {
		> b, > strong {
			font-size: 1.2em;
		}
		> strong {
			font-size: 1.6em;
		}
	}
}
.img-deal {
	border: 1px solid @gray-light;
	background-color: white;
	padding:5px;
	width: 100%;
}
// Classified Detail
// -------------------------
.classified-details {
	.classified-info {
		text-align: center;
		margin: @grid-gutter-width/2 0;
		.desc-title {
			padding: @grid-gutter-width/3;
			background-color: lighten(@brand-primary, 40%);
			color: contrast(lighten(@brand-primary, 40%), black, white, 40%);
			margin-right: -3px;
			.text-overflow();
		}
		.desc-item {
			padding: @grid-gutter-width/3;
			background-color: lighten(@brand-primary, 8%);
			color: contrast(lighten(@brand-primary, 8%), black, white, 40%);
			text-transform: uppercase;
			.text-overflow();
				webkit-flex: 2; /* Safari 6.1+ */
			       -ms-flex: 2; /* IE 10 */ 
			    	   flex: 2;
		}
	}
}

.classified-map {
	margin: @grid-gutter-width 0 @grid-gutter-width -4px;
	
	@media (min-width: @screen-md) {		
		.map {
			height: 150px;
		}
	}
	@media (max-width: @screen-md) and (min-width: @screen-sm) {		
		img {
			max-width: 100.6%;
		}
	}
	@media (max-width: @screen-sm) {
		.map {
			height:180px;
			img {
				height: 100%;
				max-width: 100%;
			}
		}
	}
}
// Social Buttons
// -------------------------
.nav.nav-pills {
	background-color: lighten(@gray, 28%);
	margin: @grid-gutter-width/2 0;
	> li {
		> a {
			color: @gray-darker;
			font-weight: 600;
			position: relative;
		}
		&.active a, > a:hover, > a:focus {
			background-color: transparent;
			&:before {
				content: "";
				position: absolute;
				bottom: 0;
				border-width:8px;
				border-style: solid;
				right: 40%;
				border-color: transparent transparent white transparent;
			}
		}
	}
}

// Social Buttons
// -------------------------
.social-sharing {
	margin:@grid-gutter-width/2 auto;
}