//
// Main Structures
// --------------------------------------------------

// Header
// -------------------------
.header-brand {
	background-color: @header-bg;
	.brand {
		h1 {
			margin: 0;
			line-height: 1;
			height: 113px;
			display: table;
			> a {
				display: table-cell;
				vertical-align: middle;
			}
		}
	}
	@media (max-width: @grid-float-breakpoint) {
		display: none;
	}
}

//Header Navbar 
	@media(min-width: @screen-sm) {
		nav.navbar {
			margin: 0 0 0 -@grid-gutter-width/2;
			.navbar-header {
				overflow: hidden;
				width: 0;
			}
		}
	}

	@media (max-width: @grid-float-breakpoint) {
		.navbar-header {
			.navbar-toggle {
				&.search-toggle {
					.fa {
						color: white;
					}
				}
				.navbar-label {
					color: lighten(@gray-light, 15%);
				}
			}
			.navbar-brand {
				.brand-logo {
					max-height: 40px;
					padding:5px 0;
				}
			}
		}
	}


// Header Search
// -------------------------
.search-header {
	color: contrast(@header-bg, @gray-darker, @gray-lighter, 40%);
	label {
		font-weight: 400;
		font-size: @font-size-base;
	}
	.form-control {
		height: 31px;
	}
	@media (min-width: @screen-sm) {
		margin-top: @grid-gutter-width;
		margin-bottom: @grid-gutter-width/2;
	}
}


// Footer
// -------------------------
.footer-static-bottom {
	background-color: @footer-bg;
	color: lighten(@footer-links, 10%);
	line-height: 1.9;
	font-size: @font-size-small;
	padding-bottom:  @grid-gutter-width;
	a {
		color: @footer-links;
		&:hover, &:focus {
			color: lighten(@footer-links, 10%);
			text-decoration: underline;
		}
		&:visited {
			opacity: 0.8;
		}
	}
	h5 {
		margin-top: 10px;
		line-height: 3.5;
		font-size: 1.3em;
		border-bottom: 1px solid @gray-dark;
		font-weight: 400;
	}
	p.small {
		a {
			color: lighten(@brand-primary, 20%);
		}
	}
}


// Spaces
// -------------------------
@media (max-width: @screen-sm) {
	.break-sm {
		margin-bottom: @grid-gutter-width;
		display: block;
		clear: both;
	}	
	.text-center-sm {
		text-align: center !important;
	}
}

@media (max-width: @screen-xs) {
	.break-xs {
		margin-bottom: @grid-gutter-width;
		display: block;
		clear: both;
		&.pull-right, &.pull-left {
			float:none !important;
			margin: 10px 0;
		}
	}
	.text-center-xs {
		text-align: center !important;
	}
}

// Sections
// -------------------------
section.block {
	margin-bottom: @grid-gutter-width;
	clear: both;
}

// Background Image
// -------------------------
.bg-image {
  position: absolute;
  z-index: -1;
  width: 100%;
  right: 0;
  left: 0;
  img {
  	max-width: 100%;
  	width: 100%;
  }
}


// Banners
// -------------------------
.ad-banners img {
	max-width: 100%;
}

@media (min-width: @grid-float-breakpoint) {
    .banner-small img {
		float: right;
	}
	.banner-bottom img{
		width: 100%;
	}
}

@media (max-width: @grid-float-breakpoint) {
    .banner-small {
		img {
			margin: 10px auto;
		}
	}
	.ad-banners {
		overflow-x: hidden;
	}
}
.sponsored-ads {
	background-color: white;
	border: 1px solid @gray-light;
	font-size: @font-size-small;
	text-align: center;
	width: 100%;
	display: block;
	padding-bottom: 2px;
	.title {
		font-weight: 600;
		color: @brand-info;
	}
	.title, .text {		
		clear: both;
		display: block;
	}
	.text {
		color: @gray;
	}
	a {			
		&:hover, &:visited, &:focus {
			text-decoration: none;
		}
	}
	@media (max-width:@screen-sm) and (min-width:@screen-xs) {
		width: 50%;
		margin: 0 auto;
	}
}



// Search Filters
.search-filter {
	@media (min-width: @grid-float-breakpoint) {
		&.collapse {
			height: auto !important;
			display: block;
		}
	}
	.btn-filter {
		background-color: transparent;
		padding:  8px @padding-base-horizontal*.4;
		color: @brand-primary;
		float: left;
		&:hover, &:focus, &:active {
			color: lighten(@brand-primary, 10%);
			outline: none;
			box-shadow: none;
		}
		.fa-caret {
			.fa-icon();
			&:before {
				content: @fa-var-caret-right;
			}
		}
		&[aria-expanded="true"] {
			.fa-caret {
				&:before {
					content: @fa-var-caret-down;
				}
			}
		}
	}
	.panel-content {
		padding: @panel-body-padding;
		.list-group-item {
			background-color: lighten(@gray-lighter, 1%);
		}
	}
	.panel-body {
		background-color: lighten(@gray-lighter, 1%);
		border-bottom: 1px solid @gray-light;
		+ .panel-collapse + .panel-body {
			border-top: 1px solid @gray-light;
		}
	}
	.list-group {
		.list-group-item {
			border:none;
			&.active {
				background-color: @gray-lighter;
				color: @gray-dark;
			}
		}
	}
	.list-categorytree {
		margin-bottom: 0;
		.list-group-item {
			padding: 0;
			> a {
				 padding: @padding-base-horizontal  @padding-base-horizontal   @padding-base-horizontal @padding-base-horizontal*1.4;
				 display: block;
				 color: @gray-dark;
				 &:hover, &:focus, &.active {
				 	background-color: @gray-lighter;
				 	text-decoration: none;
				 	color: @gray-dark;
				 }
			}
		}
	}
	ul > .collapse > .list-categorytree {
		margin-left: 15px;
	}
}

//Results Page
.results-page {
	> p {
		margin-top: @grid-gutter-width/2;
	}
}

//Internal Pages

//View phone and View fax hidden
.hidden-info {
	cursor: pointer;
	color: @brand-primary;
}

//Flex Rows
.flex {
	display: -webkit-flex; /* Safari */
	display: flex; 
	-webkit-align-items: center;
	align-items: center;
	-webkit-justify-content: space-between; /* Safari */
	justify-content: space-between;
	&.flex-row {		
		-webkit-flex-direction: row;
		flex-direction: row;
	}
	&.flex-columns {		
		-webkit-flex-direction: column; /* Safari */
  		flex-direction: column;
	}
	> .flex-unit {
		webkit-flex: 1; /* Safari 6.1+ */
	       -ms-flex: 1; /* IE 10 */ 
	    	   flex: 1;
	}
}
