.panel-theme {
	margin: 0 @grid-gutter-width/2;
	box-shadow: none;
	.panel-heading, p {
		text-align: center;
	}
	.panel-heading > h2 {		
		font-weight: 400;
		text-transform: uppercase;
		font-size: 1.6em;
	}
	.panel-body {		
		border: 1px solid @gray-light;
		padding: @grid-gutter-width  @grid-gutter-width  @grid-gutter-width/2  @grid-gutter-width ;
	}
	p {
		margin: @grid-gutter-width/2 0;
	}
}