//
// Summary
// --------------------------------------------------


// Classic Summary
// --------------------------------------------------
.summary-box {
	border: 1px solid @gray-light;
	margin-bottom: @grid-gutter-width;
	&.summary-backlink {
		border-color: @brand-highlight;
	}
	&.summary-post {
		border-color: transparent;
		border-bottom-color: @gray-light;
		> .summary-content {
			padding:0;
		}
		.blog-comments {
			margin-top: 10px;
			a {
				color: @gray-darker;
				&:hover, &:focus {
					color: @brand-primary;
				}
			}
		}
	}
	> .summary-content {
		padding: @grid-gutter-width/2;
		.deal-tag {			
			margin-right: -@grid-gutter-width/2 - 1;
			margin-top: 10px;
			> a {
				font-size: 1.2em;
				padding: 10px;
				background-color: @brand-highlight;
				color: white;
				font-weight: 400;
				&:hover, &:focus {
					text-decoration: none;
					background-color: darken(@brand-highlight, 10%);
				}
			}
		}
		.event-date {
			text-align: right;
			b {
				color: @brand-primary;
				font-weight: 400;
				display: block;
				clear: both;
			}
		}
		h3 {
			margin:0;
			color: @gray-dark;
			a {
				color: @gray-darker;
				&:visited {
					color: @gray-dark;
				}
			}
		}
		> p, .media-body > p {
			color: @gray;
		}
		address {
			margin-bottom: 0;
		}
		.media-body > .media {
			margin-top: 0;
		}		
		@media (max-width: @screen-md)  {

			> .media > .media-right {
				display: block;
				float: none;
				clear: both;	
				padding-left: 0;
				width: 100%;
			}
		}	
		@media (max-width: @screen-md) and (min-width: @screen-xs) {			
			> .media .media {
				margin-bottom: 10px;
			}
		}
		@media (max-width: @screen-smxs) {
			> .media .media {
				display: block;
				float: none;
				clear: both;	
				padding-left: 0;
				width: 100%;
				margin-bottom: 10px;
			}
			> .media > .media-left {
				display: block;
				width: 100%;
				padding-right: 0;
				img {
					width: 100%;
				}
			}
		}
		@media (max-width: @screen-xs)  {
			.media > div {
				display: block;
				float: none;
				clear: both;	
				padding-left: 0;
				width: 100%;
				text-align: left;
			}
		}

	}
	.summary-badges {
		@media (min-width: @screen-md) {				
			img {
				margin-bottom: 10px;
			}	
		}
		@media (max-width: @screen-md) {
			margin-bottom: 10px;
			img {
				display: inline-block; 
				margin-right:10px;
			}
		}
	}
	.contact-info {
		text-align: right;
		span + span {
			display: block;
		}
	}
	> .summary-footer {
		background-color: @gray-lighter;
		padding: @grid-gutter-width/4 @grid-gutter-width/2;
		> span + span {
			&:before {
				content: " | ";
				display: inline-block;
			}
		}
		.actions-right {
			float: right;
			font-size: 20px;
			.fa {
				vertical-align: super;
			}
		}
		@media (min-width: @screen-smxs) {
			.article-author {
				float: right;
			}
		}
		@media (max-width: @screen-xs)  {
			text-align: center;
			.actions-right {
				margin-top: 10px;
				float: none;
				display: block;
				clear: both;
			}
		}
	}
}