//
// Social Links
// --------------------------------------------------

// Generic Social Rounded Links
// -------------------------
.social-links {
	background-color: @gray-light;
	box-shadow: inset 0 1px 2px white;
	color: @gray-darker;
	line-height: 18px;
	padding:5px;
	border-radius: 30px;
	display: inline-block;
	text-align: center;
	margin-bottom: 5px;
	.fa {
		color: @gray-darker;
		min-width: 18px;
		vertical-align: middle;
		font-size: 18px;
	}
}

//Social Buttons
// ----------------------
//Facebook
.btn-facebook {
	background-color: #5c6a90;
	color: white;
	&:hover, &:focus{
		color: white;
		background-color: darken(#5c6a90, 10%);
	}
	.fa {
		font-size: @font-size-large;
		vertical-align: middle;
		margin-right: 7px;
	}
}
//Google
.btn-google {
	background-color: #dd4b39;
	color: white;
	&:hover, &:focus{
		color: white;
		background-color: darken(#dd4b39, 10%);
	}
	.fa {
		font-size: @font-size-large;
		vertical-align: middle;
		margin-right: 7px;
	}
}

.btn-rss {
	background-color: #f4b83f;
	color: white;
	&:hover, &:focus{
		color: white;
		background-color: darken(#f4b83f, 10%);
	}
	.fa {
		font-size: @font-size-large*1.5;
		vertical-align: bottom;
	}
}


//Social Icons
[class*="social-link-"] {
	vertical-align: middle;
	font-size: 1.4em;
	margin-left: .3em;
}


