//
// Calendar Events
// --------------------------------------------------


// Table Calendar
// -------------------------
.table-calendar {
	> tbody > tr {
		> th {
			text-align: center;
		}
		> td {
			border-top: none;
			text-align: center;
			padding: 0;
			&:hover, &.active, &:hover > a, &.active > a{
				background-color: @brand-success;
				color: white;
				cursor: pointer;
			}
			a {
				padding: @grid-gutter-width/5;
				display: block;
				&:hover, &:focus, &.active {
					text-decoration: none;
				}
			}
		}
	}
}

// List Calendar Date
// -------------------------

.list-calendar {
	ul {
		display: table;
		width: 100%;
		margin: 0 0 -7px 0;
		li {
			display: table-cell;
			background-color: @brand-success;
			text-align: center;
			color: white;
			padding:10px;
			font-size: .8em;
			b {
				display: block;
				color: white;
				text-transform: uppercase;
			}
			&.active, &:hover, &:focus {
				background-color: @brand-info;
				cursor: pointer;
			}
		}
	}
}
