/* #Input hidden*/
.hiddenFile-box {
	.hiddenFile {
		input[type="file"] {
			  cursor: pointer;
			  opacity: 0;
			  filter: alpha(opacity=00);
			  padding: 0;
			  margin: -169px 0 0 73px;
			  width: 136px;
			  height: 166px;
			  position: absolute;
		}
		button {
			border: 1px solid #ccc;
			cursor: pointer;
			font-size: 10px;
			height: 30px;
			outline: medium none;
			width: 100px;
			margin: 5px 0;
		}
	}
}

