//
// Columns
// --------------------------------------------------


//
// Mixin Columns
// --------------------------------------------------
.columns-default(@n, @w) {
	-webkit-column-count: @n; /* Chrome, Safari, Opera */
    -moz-column-count: @n; /* Firefox */
    column-count: @n;
    -webkit-column-gap: @grid-gutter-width; /* Chrome, Safari, Opera */
    -moz-column-gap: @grid-gutter-width; /* Firefox */
    column-gap: @grid-gutter-width;
    -webkit-column-width: @w; /* Chrome, Safari, Opera */
    column-width: @w;
}
.columns-line(@n, @w) {
	-webkit-column-count: @n; /* Chrome, Safari, Opera */
    -moz-column-count: @n; /* Firefox */
    column-count: @n;
    -webkit-column-gap: @grid-gutter-width; /* Chrome, Safari, Opera */
    -moz-column-gap: @grid-gutter-width; /* Firefox */
    column-gap: @grid-gutter-width;
    -webkit-column-width: @w; /* Chrome, Safari, Opera */
    column-width: @w;
    -webkit-column-rule:1px solid @gray-light; /* Chrome, Safari, Opera */
    -moz-column-rule:1px solid @gray-light; /* Firefox */
    column-rule:1px solid @gray-light;
}

//
// List Columns
// --------------------------------------------------
.list-columns {
	 > ul {
		.columns-default(3, 100px);
	}
}